.footer {
  color: $bodyTextColor;
  padding: 40px 0;
  position: relative;
  background-color: #efefef;

  @media (min-width: 992px) {
    padding: 80px 0;
  }

  > .container {

  }

  &__soc {
    text-align: center;
    max-width: 320px;
    margin: 0 auto;

    @media (min-width: 992px) {
      max-width: 100%;
      padding-top: 0;
      display: flex;
      justify-content: flex-start;
    }


    .footer__soc-item {
      display: inline-block;
      border-radius: 50%;
      width: 50px;
      height: 50px;
      border: none;
      //padding: 8px;
      background-color: $primaryColor;
      transition: all .3s ease;
      margin-bottom: 20px;

      @media (min-width: 992px) {
        margin-bottom: 0;
      }

      &:not(.wideBtn):hover {
        transform: translateY(-3px);
        transition: all .3s ease;

        img {
          transform: scale3d(1.08,1.08,1);
          transition: all .3s ease;
        }
      }

      &:not(:last-child) {
        //margin-right: 10px;
      }

      @media (min-width: 992px) {
        align-self: center;
        //padding: 4px;
      }

      @media (min-width: 1200px) {
        //padding: 8px;
      }

      img {
        max-width: 100%;
        transform: scale3d(1,1,1);
        transition: all .3s ease;
      }
    }


    .tlgrmBtn {
      background-color: #2da5d8;
    }

    .whtspBtn {
      background-color: #00a500;
    }

    .wideBtn {
      width: auto;
      border-radius: 30px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 8px 25px;
      max-width: 220px;
      margin-left: auto;
      margin-right: auto;

      @media (min-width: 992px) {
        width: 280px;
        margin-left: 0;
        margin-right: 0;
      }
    }
  }

  &__logo {
    height: 80px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    position: relative;
    z-index: 10;
    margin-bottom: 30px;

    @media (min-width: 992px) {
      justify-content: flex-start;
      margin-bottom: 80px;
    }

    @media (min-width: 1200px) {
      margin-bottom: 120px;
    }


    > img {
      height: inherit;
      width: auto;
    }

    span {
      color: $bodyTextColor;
      font-size: 24px;
      font-weight: bold;
      text-transform: uppercase;
      align-self: center;
      margin-left: 8px;
      margin-bottom: 5px;
      transition: all 0.2s ease;
    }
  }

  &__title {
    font-weight: bold;
    font-size: 20px;
    text-align: center;
    margin-bottom: 30px;

    @media (min-width: 992px) {
      text-align: left;
    }
  }

  &__text {
    @media (max-width: 991px) {
      max-width: 400px;
      text-align: center;
      margin-left: auto;
      margin-right: auto;
    }
  }

  &__copyright {
    font-size: 1.125em;

    @media (min-width: 992px) {
      margin-top: 80px;
    }

    @media (min-width: 1200px) {
      margin-top: 120px;
    }
  }
}

.footer__auth {
  .btn-second {
    background-color: #fff;
  }
}