.advantages {
  overflow: hidden;
}

.advantegaes-container {
  display: flex;
  justify-content: space-between;
  flex-direction: column;

  @media (min-width: 576px) {
    flex-direction: row;
    gap: 30px;
    flex-wrap: wrap;
  }

  @media (min-width: 1200px) {
    flex-wrap: nowrap;
  }
}

.advantegaes-list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 50px;
  max-width: 250px;
  margin-right: auto;
  margin-left: auto;

  @media (min-width: 576px) {
    max-width: calc(50% - 15px);
    flex-grow: 1;
  }

  @media (min-width: 992px) and (max-width: 1199px) {
    width: 100%;
    flex-direction: row;
    max-width: 100%;
    flex-wrap: nowrap;
    margin-bottom: 30px;
    gap: 30px;
  }

  @media (min-width: 1200px) {
    max-width: 20%;
    gap: 15px;
  }

  @media (min-width: 1400px) {
    gap: 30px;
    max-width: unset;
    flex-grow: 1;
    flex-basis: 15%;
  }

  &:not(:first-child) {
    margin-top: 50px;

    @media (min-width: 576px) {
      margin-top: 0;
    }

    @media (min-width: 992px) {
      margin: 0;
    }
  }
}

.advantegaes-list__item {
  width: 100%;
  height: 150px;
  background-color: white;
  border-radius: 15px;
  box-shadow: 0 1px 10px -5px rgba(0, 0, 0, 0.1);
  position: relative;
  padding: 31px 35px 13px 35px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  z-index: 5;
  cursor: default;

  @media (min-width: 576px) {
    height: 160px;
  }

  @media (min-width: 1200px) {
    padding: 15px;
    min-height: 235px;
  }

  @media (min-width: 1400px) {
    min-height: 180px;
  }

  &:hover {
    .advantegaes-list__item_icon {
      opacity: 0;
      transform: translateY(5px);
    }

    .advantegaes-list__item_title {
      transform: translateY(-75px);

      @media (min-width: 576px) {
        transform: translateY(-85px);
      }

      @media (min-width: 1200px) {
        transform: translateY(-130px);
      }

      @media (min-width: 1400px) {
        transform: translateY(-80px);
      }
    }

    .advantegaes-list__item_desc {
      opacity: 1;
      margin-top: 10px;

      @media (min-width: 576px) {
        margin-top: 8px;
      }

      @media (min-width: 1200px) {
        margin-top: 25px;
      }

      @media (min-width: 1400px) {
        margin-top: 15px;
      }
    }
  }
}

.advantegaes-list__item_icon {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 135px;
  height: 135px;
  top: -40px;
  left: 50%;
  margin-left: -65.5px;
  background-image: url('/images/icons/advanteges.png');
  background-position: center;
  color: white;
  font-size: 25px;
  transition: all 0.3s;
  background-size: cover;

  @media (min-width: 1200px) and (max-width: 1399px) {
    top: 12px;
  }

  svg {
    width: 40px;
    height: 40px;
    fill: #fff;
  }
}

.advantegaes-list__item_title {
  text-align: center;
  font-size: 16px;
  margin-bottom: 0;
  height: 44px;
  line-height: 19px;
  font-weight: bold;
  transition: all 0.3s;

  @media (min-width: 1200px) {
    margin-bottom: 20px;
  }

  @media (min-width: 1400px) {
    margin-bottom: 20px;
  }
}

.advantegaes-list__item_desc {
  position: absolute;
  height: 80%;
  width: 93%;
  font-size: 11px;
  top: 55px;
  text-align: center;
  opacity: 0;
  color: black;
  transition: all 0.2s;

  @media (min-width: 1200px) {
    font-size: 12px;
    width: 80%;
  }

  @media (min-width: 1400px) {
    width: 85%;
  }
}

.advantegaes-text {
  margin-top: 45px;
  background-color: white;
  border-radius: 15px;
  box-shadow: $box-shadow-flat;
  padding: 30px;

  @media (min-width: 576px) {
    margin-top: 10px;
  }

  @media (min-width: 992px) {
    margin-top: 30px;
  }

  @media (min-width: 1200px) {
    width: 60%;
    margin-top: 0;
  }

  @media (min-width: 1400px) {
    width: 55%;
  }

  p {
    font-size: 14px;

    @media (min-width: 1200px) {
      font-size: 16px;
    }

    @media (min-width: 1400px) {
      font-size: 15px;
      margin-bottom: 10px;
    }
  }
}

.advantegaes-text__title {
  font-weight: bold;
}