/** Базовые анимации: */
@import 'keyframes';

/** Стили для полноэкранного блока: */
// @import "fullscreen";

/*
* (i) Стили будут применяться ко
* всем классам содержащим *__container
* Например header__container, main__container и т.д.
*/
[class*='__container'] {
  max-width: rem($maxWidthContainer);
  margin: 0 auto;
  padding: 0 rem($mainOffset);
}

// Стили отзывчивых картинок (IBG) ============================================
[class*='-ibg'] {
  position: relative;

  img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
  }
}

[class*='-ibg_contain'] {
  img {
    object-fit: contain;
  }
}

// Оболочка ===================================================================
.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;

  /** Поменять на clip - если используется в проекте position: sticky */
  overflow: hidden;
}

.page {
  position: relative;
  flex: 1 1 auto;

  [data-observ] {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: rem(2);
    background: transparent;
    pointer-events: none;
  }

  &_start {
    padding-top: rem($headerHeight);
  }
}

a {
  text-decoration: none;
}

.backplate {
  background: #fff;
  padding: 10px 10px 10px 10px;
  border-radius: 15px;
  box-shadow: $box-shadow-flat;
}

.btn {
  border-radius: 15px;
}

.btn-action {
  color: #fff;
  //box-shadow: 0 12px 35px 8px rgba(17, 6, 51, 0.3);
  border: none;
  position: relative;
  overflow: hidden;

  &::before {
    content: 'Регистрация';
    position: absolute;
    left: 50%;
    margin-left: -100px;
    top: 50%;
    margin-top: -100px;
    width: 200px;
    height: 200px;
    background: $primaryGradientC1;
    background: -webkit-gradient(linear, left top, right top, from($primaryGradientC1), to($primaryGradientC2));
    background: linear-gradient(to right, $primaryGradientC1, $primaryGradientC2);
    z-index: 1;
    transform-origin: center;
    border-radius: 100px;
    transition: all 0.3s;
  }

  span {
    color: #fff;
    position: relative;
    z-index: 1;
    transition: all 0.3s;
  }

  &:hover {
    box-shadow: $box-shadow-flat;
    color: #5d55ed;
    background-color: #fff;

    span {
      color: #5d55ed;
    }

    &::before {
      transform: scale(0);
    }
  }
}

.btn-second {
  color: #595959;
  //box-shadow: 0 12px 35px 8px rgba(17, 6, 51, 0.3);
  border: none;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: row !important;

  svg {
    width: 35px;
    height: 22px;
    margin-top: auto;
    margin-bottom: auto;
    fill: #595959;
    transition: all .2s ease;

    path {
      fill: #595959;
      transition: all .2s ease;
    }
  }

  span {
    margin-top: auto;
    margin-bottom: auto;
  }

  &::before {
    content: '';
    position: absolute;
    left: 50%;
    margin-left: -100px;
    top: 50%;
    margin-top: -100px;
    width: 200px;
    height: 200px;
    background: #dddddd;
    background: -webkit-gradient(linear, left top, right top, from(#dddddd), to(#dddddd));
    background: linear-gradient(to right, #dddddd, #dddddd);
    z-index: -1;
    transform-origin: center;
    border-radius: 100px;
    transition: all 0.3s;
  }

  &:hover {
    box-shadow: $box-shadow-flat;
    color: #000;
    background-color: #fff;

    &::before {
      transform: scale(0);
    }

    svg {
      fill: #000;

      path {
        fill: #000;
      }
    }
  }
}

.btn-simple {
  border-radius: 10px;
  background-color: $accentColor3;
  color: #fff;
  border: none;

  &:hover {
    background-color: $accentColor;
    color: #fff;
  }

  &:active {
    background-color: $accentColor !important;
    color: #fff !important;
  }
}

.btn-empty {
  filter: grayscale(1);
}

section.padding {
  padding: 45px 0;

  @media (min-width: 768px) {
    padding: 70px 0;
  }

  @media (min-width: 992px) {
    padding: 100px 0;
  }
}

.section-title {
  margin-bottom: 30px;
  font-size: 28px;

  @media (min-width: 768px) {
    margin-bottom: 10px;
    font-size: 32px;
  }

  @media (min-width: 992px) {
    font-size: 36px;
  }
}

.section-subtitle {
  @media (min-width: 768px) {
    margin-bottom: 45px;
  }
}

img {
  max-width: 100%;
}