.header {
  //height: calc($headerHeight + $haderPadding * 2);
  //padding: $haderPadding 0;
  position: relative;
  margin-bottom: -92px;
  width: 100vw;
  overflow: hidden;

  @media (min-width: 992px) {
    width: 100%;
  }

  &.overflow {
    overflow: visible;
  }

  > .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 16px;
    padding-bottom: 16px;

    @media (min-width: 992px) {
      //padding-top: 30px;
      //padding-bottom: 30px;
    }
  }

  &__logo {
    display: flex;
    flex-wrap: nowrap;
    //height: $headerHeight;
    height: 60px;
    //margin-right: 80px;
    position: relative;
    user-select: none;
    z-index: 10;
    padding: 10px 12px 10px 10px;

    @media (min-width: 992px) {
      margin-right: 14px;
    }

    @media (min-width: 1200px) {
      margin-right: 40px;
    }

    &:hover {
      img {
        //transform: rotate(-5deg);
        transition: all .2s ease;
      }

      span {
        //color: $primaryColor;
        transition: all .2s ease;
        //text-shadow: 3px 3px 0 #3d0d3e;
      }
    }

    span {
      color: #3a2685;
      //text-transform: uppercase;
      font-size: 24px;
      line-height: 100%;
      align-self: center;
      margin-left: 5px;
      font-weight: bold;
      transition: all .2s ease;
      //text-shadow: 0px 0px 10px rgba(0,0,0,0.5);
      //text-shadow: 1px 1px white, -1px -1px #c1c1c1;
      margin-top: 2px;

      @media (min-width: 360px) {
        font-size: 24px;
      }

      @media (min-width: 375px) {
        font-size: 24px;
      }
    }

    img {
      width: auto;
      height: 100%;
      transition: all .2s ease;
    }
  }

  &__menu {
    padding-top: 92px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    transform: translateX(100%);
    background-color: $bodyBgColor;
    //opacity: 0;
    transition: all .3s ease;
    z-index: 8;

    @media (min-width: 992px) {
      position: static;
      width: auto;
      height: auto;
      transform: none;
      opacity: 1;
      background-color: transparent;
      padding-top: 0;
      flex-grow: 1;
    }

    .container {
      @media (min-width: 992px) {
        width: auto;
        max-width: 100%;
        padding: 0;
        margin: 0;
        display: flex;
        justify-content: flex-end;
      }
    }
  }

  &__nav {
    display: flex;
    flex-direction: column;
    font-weight: 500;
    text-align: center;
    padding-top: 30px;
    padding-bottom: 30px;

    @media (min-width: 992px) {
      flex-direction: row;
      padding-top: 0;
      padding-bottom: 0;
      margin: 0 auto;
      flex-wrap: wrap;
      flex-grow: 1;
    }

    a, button {
      font-size: 20px;

      @media (min-width: 992px) {
        font-size: 16px;
      }

      @media (min-width: 1200px) {
        font-size: 18px;
      }

      @media (min-width: 1400px) {
        font-size: 20px;
      }
    }
  }

  &__navItem {
    padding-top: 9px;
    padding-bottom: 7px;

    &:hover {
      span {
        color: #5d55ed;
      }
    }

    @media (min-width: 992px) {
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding-top: 0;
      padding-bottom: 0;
    }

    span {
      //padding-bottom: 4px;
      border-bottom: 2px solid transparent;
      transition: all .2s;
      color: $bodyTextColor;
      font-weight: 400;
    }

    &.active {
      span {
        border-bottom-color: #d1d1d1;
      }
    }

    &:not(.active):hover {
      span {
        border-bottom-color: #5d55ed;
      }
    }

    &:not(:last-child) {
      @media (min-width: 992px) {
        margin-right: 18px;
      }

      @media (min-width: 1200px) {
        margin-right: 50px;
      }
    }
  }

  &__toggler {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 10px;
    position: relative;
    z-index: 10;
    height: 60px;
    width: 60px;

    @media (min-width: 992px) {
      display: none;
    }

    .toggler__line {
      display: inline-block;
      height: 2px;
      background-color: #7f7f7f;
      width: 30px;
      margin: 5px 0;
      transition: all .3s ease;
      margin-left: auto;
      margin-right: auto;
    }
  }

  &__auth {
    text-align: center;
    max-width: 165px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;

    @media (min-width: 992px) {
      max-width: 280px;
      margin: 0;
      flex-direction: row;
    }

    a, button {
      height: 50px;
      text-transform: uppercase;
      font-weight: bold;
      display: flex;
      flex-direction: column;
      justify-content: center;

      &:last-child {
        padding-left: 5px;
      }

      @media (min-width: 992px) {
        &:first-child {
          border-top-right-radius: 0;
          border-bottom-right-radius: 0;
        }
        &:last-child {
          border-top-left-radius: 0;
          border-bottom-left-radius: 0;
          color: $bodyTextColor;

          &::before {
            margin-left: -80px;
            top: 50%;
            margin-top: -75px;
            width: 145px;
            height: 145px;

            background: -webkit-gradient(linear, left top, right top, from(#fff), to(#fff));
            background: linear-gradient(to right, #fff, #fff);
          }

          &:hover {
            background-color: #bbbbbb;
            color: #fff;

            svg {
              fill: #fff;

              path {
                fill: #fff;
              }
            }
          }
        }
      }
    }

    > *:not(:last-child) {
      margin-bottom: 20px;

      @media (min-width: 992px) {
        //margin-right: 24px;
        margin-bottom: 0;
      }
    }
  }

  &__soc {
    padding-top: 30px;
    text-align: center;
    max-width: 320px;
    margin: 0 auto;

    @media (min-width: 992px) {
      padding-top: 0;
      display: flex;
      margin: 0 0 0 20px;
    }

    a, button {
      display: inline-block;
      border-radius: 50%;
      width: 50px;
      height: 50px;
      border: none;
      padding: 8px;
      background-color: $primaryColor;
      transition: all .3s ease;

      &:hover {
        background-color: #fff;
        transform: scale3d(1.15,1.15,1);
        transition: all .3s ease;
        box-shadow: $box-shadow-flat;

        img {
          filter: brightness(0.5);
        }
      }

      &:not(:last-child) {
        margin-right: 10px;
      }

      @media (min-width: 992px) {
        width: 32px;
        height: 32px;
        align-self: center;
        padding: 4px;
      }

      @media (min-width: 1200px) {
        width: 40px;
        height: 40px;
        align-self: center;
        padding: 8px;
      }

      span {

      }

      img {
        max-width: 100%;
        transform: scale3d(1,1,1);
        transition: all .3s ease;
      }
    }

    .tlgrmBtn {
      background-color: #2da5d8;

      img {
        position: relative;
        top: 0;
        left: -1px;

        @media (min-width: 992px) {
          top: -2px;
          left: -1px;
        }
      }

    }

    .whtspBtn {
      background-color: #00a500;
    }
  }

  &__btn--signin {
    //padding-left: 50px;
    //padding-right: 50px;
  }

  &.m-menu-open {

    position: fixed;
    touch-action: auto;

    .header__menu {
      //opacity: 1;
      transform: translateX(0);
      transition: all .3s ease;
      position: fixed;
      touch-action: auto;

      .container {
        //overflow-y: scroll;
        //height: 2000px;
      }
    }

    .header__toggler {
      .toggler__line {
        &--1 {
          width: 36px;
          transform: translateX(0px) translateY(12px) rotate(45deg);
        }

        &--2 {
          transform: scale3d(0,0,0);
          opacity: 0;
        }

        &--3 {
          width: 36px;
          transform: translateX(0px) translateY(-12px) rotate(-45deg);
        }
      }
    }
  }
}
