.geo {
  overflow: hidden;

  .section-title {

    @media (min-width: 1200px) {
      margin-bottom: 5rem;
    }
  }

  .map-and-cities {
    position: relative;
    height: 100%;
    width: 100%;

    img {
      width: 100%;
      height: auto;
    }

    .city-on-map {
      position: absolute;
      width: 5px;
      height: 5px;
      top: 10%;
      pointer-events: none;

      .city-point {
        display: block;
        width: 5px;
        height: 5px;
        background-color: $city-point;
        border-radius: 50%;
        pointer-events: auto;
        //animation: pointScale 5s infinite ease-in-out;
        transition: all .2s ease !important;
        transform: scale3d(1,1,1);
        cursor: pointer;
        z-index: 5;

        &:hover, &.hover {
          transition: all .2s ease !important;
          animation: none;
          transform: scale3d(1.8,1.8,1.8);
          background-color: #fff;
          box-shadow: 1px 1px 1px 0 rgba(0,0,0,0.5);
          z-index: 10;

          ~ .city-name {
            opacity: 1;
            transition: all .6s ease;

            &::before {
              transition: all .2s ease;
              animation: lineAngle .1s ease-in forwards;
            }

            &::after {
              animation: lineWidth .2s ease-in forwards .1s;
              transition: all .2s ease;
            }
          }

          ~ .circle-full {
            background-color: transparent;
            opacity: 0 !important;
          }

          ~ .circle-border {
            opacity: 0 !important;
            border-color: transparent;
          }
        }
      }

      .circle-full {
        display: block;
        position: absolute;
        top: 3px;
        left: 2px;
        width: 10px;
        height: 10px;
        background-color: $city-circle;
        border-radius: 50%;
        opacity: 0;
        transform-origin: center;
        transform: translateY(-50%) translateX(-50%) scale3d(1,1,1);
        animation-delay: 10s;
        animation: pointFill 6s infinite ease-in;
        pointer-events: none;
      }

      .circle-border {
        position: absolute;
        top: 3px;
        left: 2px;
        width: 10px;
        height: 10px;
        border: 1px solid $city-point;
        border-radius: 50%;
        opacity: 0;
        transform-origin: center;
        transform: translateY(-50%) translateX(-50%) scale3d(1,1,1);
        animation-delay: 10s;
        animation: pointBorder 5s infinite ease-in-out;
        pointer-events: none;
      }

      span {
        opacity: 0;
      }

      .city-name {
        opacity: 0;
        font-size: 20px;
        position: relative;
        top: -37px;
        left: 15px;
        transition: all .1s ease;
        pointer-events: none;
        user-select: none;
        z-index: 5;
        //text-shadow: 1px 1px 2px #000;
        white-space: nowrap;
        font-weight: bold;
        color: $bodyTextColor;

        &::before {
          position: absolute;
          content: "";
          top: 25px;
          left: -9px;
          width: 0;
          height: 2px;
          transform: rotate(330deg);
          background-color: #fff;
          transition: all .2s ease;
          box-shadow: 1px 1px 1px 0 rgba(0,0,0,0.5);
        }

        &::after {
          position: absolute;
          content: "";
          top: 23px;
          left: -1px;
          width: 0;
          height: 2px;
          background-color: #fff;
          transition: all .2s ease;
          box-shadow: 1px 1px 1px 0 rgba(0,0,0,0.5);
        }
      }

