  .map-cities {

  }

  &__title {
    font-weight: bold;
    font-size: 28px;
    margin-bottom: 1em;
  }

  &__info {
    @media (min-width: 992px) {
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 100%;
    }
  }

  &__text {
    & + & {
      margin-top: 30px;
    }
  }

  @keyframes lineAngle {
    0% {
      opacity: 0;
      width: 0;
    }

    50% {
      opacity: 1;
    }

    100% {
      width: 9px;
    }
  }

  @keyframes lineWidth {
    0% {
      opacity: 0;
      width: 0;
    }

    50% {
      opacity: 1;
    }

    100% {
      width: 100%;
    }
  }


  @keyframes pointFill {
    0% {
      opacity: 0.05;
    }

    50% {
      opacity: 0.1;
    }

    100% {
      opacity: 0;
      transform: translateY(-50%) translateX(-50%) scale3d(8,8,8);
    }
  }

  @keyframes pointBorder {
    0% {
      opacity: 0;
    }

    50% {
      opacity: 0.1;
    }

    100% {
      opacity: 0;
      width: 120px;
      height: 120px;
    }
  }

  @keyframes pointScale {
    0% {
      transform: scale(1);
    }

    25% {
      transform: scale(0.8);
    }

    50% {
      transform: scale(1.2);
    }

    75% {
      transform: scale(0.8);
    }

    100% {
      transform: scale(1);
    }
  }
