.our-offers {
  background-color: #fdfdfd;
  
  .section-title {
    text-align: center;
  }

  .section-subtitle {
    text-align: center;
  }

  .swiper-autoheight .swiper-wrapper {
    margin-bottom: 65px;
  }

  .swiper-wrapper {
    @media (min-width: 768px) {
      margin-bottom: 65px;
    }
  }

  .swiper-slide {
    padding: 10px;
    //margin-bottom: 65px;

    @media (min-width: 768px) {
      margin-bottom: 0;
    }
  }

  .offer-item {
    background: #fff;
    border-radius: 15px;
    box-shadow: $box-shadow-flat;
    overflow: hidden;
    user-select: none;
  }

  .offer-item-header {
    border-bottom: 1px solid #e5e5e5;
    background: #515cd5;
    color: #fff;
    padding: 15px 20px;
    border-color: #efeff8;

    @media (min-width: 768px) {
      min-height: 128px;
    }

    @media (min-width: 992px) {
      min-height: unset;
    }

    @media (min-width: 1200px) {
      min-height: 128px;
    }

    @media (min-width: 1400px) {
      min-height: unset;
    }
  }

  .offer-item-header__info {
    margin-left: 30px;
  }

  .offer-item-header__wrap {
    display: flex;
  }

  .offer-item-header__title {
    color: #fff;
    font-weight: 600;
    font-size: 18px;
    line-height: 23px;
  }

  .offer-item-header__img {

    @media (min-width: 768px) {
      display: flex;
      flex-direction: column;
      justify-content: center;
      //height: 100%;
    }
    
    img {
      width: 80px;
      height: 80px;
      object-fit: contain;
    }
  }

  .offer-item-header__badge {
    border-radius: 10px;
    padding: 3px 10px;
    text-transform: uppercase;
    font-size: 10px;
    margin-top: 7px;
    display: inline-block;

    &--green {
      background: #4caf50;
    }

    &--dark {
      background: #3f51b5;
    }
  }

  .offer-item-body {
    padding: 30px 20px;
    border-radius: 15px;
    overflow: hidden;
    min-height: 290px;
    display: flex;
    flex-direction: column;

    @media (min-width: 576px) {
      min-height: 260px;
    }

    @media (min-width: 768px) {
      min-height: 308px;
    }

    @media (min-width: 992px) {
      min-height: 260px;
    }

    @media (min-width: 1200px) {
      min-height: 285px;
    }

    p {
      //font-size: 16px;
      &:first-child {
        margin-bottom: auto;
      }
    }
  }

  .offer-item-body__item {
    display: flex;

    p {
      &:nth-of-type(1) {
        flex-grow: 1;
      }

      &:nth-of-type(2) {
        flex-grow: 1;
        white-space: nowrap;
      }

      &.red-text {
        color: red;
      }
    }

    span.separator  {
      margin: 0 15px;
      opacity: 0.2;
      overflow: hidden;
    }
  }

  .swiper-button-next, .swiper-button-prev {
    top: auto;
    bottom: 0;
  }

  .swiper-button-next {
    right: 25%;

    @media (min-width: 768px) {
      right: 35%
    }

    @media (min-width: 1200px) {
      right: 40%
    }
  }

  .swiper-button-prev {
    left: 25%;

    @media (min-width: 768px) {
      left: 35%;
    }

    @media (min-width: 1200px) {
      left: 40%
    }
  }

  .swiper-pagination-bullets-dynamic {
    top: auto;
    bottom: 16px;
  }

  .swiper-pagination-bullet {
    width: var(--swiper-pagination-bullet-width,var(--swiper-pagination-bullet-size,12px));
    height: var(--swiper-pagination-bullet-height,var(--swiper-pagination-bullet-size,12px));
  }
}

.our-offers__slider {

}

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper-button-next,
.swiper-button-prev, {
  color: $primaryGradientC1;
}

.swiper-pagination-bullet-active {
  background: $primaryGradientC2;
}
