.first-screen {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 92px;
  //min-height: calc(var(--vh) * 100);
  overflow: hidden;

  position: relative;

  @media (min-width: 992px) {
    min-height: 100vh;
  }

  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    opacity: 0.4;
  }
  
  &::after {
    content: '';
    position: absolute;
    width: 420px;
    height: 490px;
    background-position: center;
    background-image: url('/images/bg/1_1.png');
    left: 0;
    bottom: -270px;
    z-index: 0;
    opacity: 0.3;

    @media (min-width: 992px) {
      opacity: 0.25;
    }
  }

  * {
    position: relative;
  }


  h1 {
    font-size: 30px;
    margin-bottom: 32px;
    font-weight: bold;
    color: $bodyTextColor;

    @media (min-width: 992px) {
      font-size: 36px;
    }

    @media (min-width: 1200px) {
      font-size: 42px;
    }
  }

  .banner {
    padding-top: 20px;
    padding-bottom: 45px;

    @media (min-width: 992px) {
      padding-top: 70px;
      padding-bottom: 70px;
    }

    @media (min-width: 992px) {
      height: calc(100vh - 92px);
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }
}

.banner-services {
  @media (min-width: 992px) {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
}

.banner-services__item {
  display: flex;
  align-items: center;
  font-size: 20px;

  @media (min-width: 992px) {
    flex-basis: 48%;
    font-size: 16px;
    margin-bottom: 0px;
  }

  svg {
    fill: #000;
    width: 45px;
    height: 45px;
    margin-right: 10px;

    path {
      fill: #000;
    }
  }
}

.banner-adventages {
  display: flex;
  flex-wrap: wrap;
  margin-top: 30px;
  margin-bottom: 30px;

  @media (min-width: 992px) {
    margin-bottom: 0;
  }
}

.banner-adventages__item {
  position: relative;
  font-size: 15px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px 9px 5px 9px;
  height: 32px;
  border-radius: 4px;
  border: 2px solid rgba(255, 255, 255, 0.21);
  margin-right: 19px;
  background-color: $accentColor3;

  svg {
    min-width: 19px;
    min-height: 19px;
    max-width: 19px;
    max-height: 19px;
    fill: #fff;
    margin-right: 7px;

    path {
      fill: #fff;
    }
  }
}


.banner-adventages__item-info-text {
  visibility: hidden;
  z-index: 10;
  position: absolute;
  left: 50%;
  bottom: 20px;
  display: inline-block;
  width: 200px;
  padding: 10px;
  border-radius: 5px;
  font-size: 14px;
  line-height: 14px;
  transform: translateX(-50%);
  box-shadow: $box-shadow-flat;
  color: $bodyTextColor;
}

.banner-adventages__item-info {
  svg {
    min-width: 15px;
    min-height: 15px;
    max-width: 15px;
    max-height: 15px;
    margin-left: 10px;
    position: relative;
    top: -1px;
  }
}

.banner-adventages__item-info:hover .banner-adventages__item-info-text {
  visibility: visible;
}

.banner-form {
  background-color: white;
  border-radius: 15px;
  padding: 35px 30px 35px 30px;
  margin-left: auto;
  position: relative;
  z-index: 2;

  @media (min-width: 360px) {
    padding: 35px 43px 35px 43px;
  }

  @media (min-width: 992px) {
    padding: 50px 43px 50px 43px;
  }

  @media (min-width: 1200px) {
    width: 88.7%;
  }
}

.input-cont {
  margin-bottom: 20px;
}

.input-cont__input:focus {
  border-color: black;
}

.input-cont__input {
  width: 100%;
  height: 30px;
  font-size: 15px;
  outline: none;
  border: none;
  border-bottom: 2px solid #e6eaf0;
  transition: all 0.3s;
}

.banner-form__header {
  margin-bottom: 20px;

  @media (min-width: 360px) {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  * {
    font-weight: bold;
  }

  p {
    margin-bottom: 10px;

    @media (min-width: 360px) {
      margin-bottom: 0;
    }
  }

  svg {
    width: 12px;
    height: 12px;
    color: $bodyTextColor;
  }

  a {
    color: $bodyTextColor;
    opacity: 0.5;
  }
}

.soc-container {
  display: flex;

  div {
    margin-right: 20px;
  }

  img {
    min-width: 40px;
    min-height: 40px;
    max-width: 40px;
    max-height: 40px;
  }
}

.form__btn_orange {
  width: 100%;
  height: 46px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'GothamPro-Bold', Arial;
  font-size: 15px;
  color: white;
  outline: none !important;
  background: $primaryGradientC1;
  background: -webkit-gradient(linear, left top, right top, from($primaryGradientC1), to($primaryGradientC2));
  background: linear-gradient(to right, $primaryGradientC1, $primaryGradientC2);
  border: none;
  border-radius: 4px;
  box-shadow: $box-shadow-flat;
  cursor: pointer;
  transition: all 0.3s;

  svg {
    width: 22px;
    height: 18px;
    fill: white;
    margin-left: 50px;
    transition: all 0.3s;
  }

  &:hover {

    svg {
      transform: translateX(10px);

    }
  }
}

.first-screen form, .modal form {
  .form-decs {
    font-size: 13px;
    color: #9092ab;
    line-height: 15px;
    margin-top: 19px;
    margin-bottom: 0;
  }
}