.previewSites {
  overflow: hidden;
  background-color: #e5e5e5;

  .section-subtitle {
    margin-bottom: 25px;

    @media (min-width: 768px) {
      margin-bottom: 60px;
    }
  }

  &__desc {
    @media (min-width: 768px) {
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 100%;
    }
  }

  &__itemArrow {
    position: relative;

    &::before, &::after {
      position: absolute;
      content: "";
      left: calc(50% - 4px);
      width: 1px;
      height: 1px;
      border: 5px solid;
      animation-iteration-count: infinite;
      animation-duration: 2s;
      animation-timing-function: ease-in-out;
      transition: all .2s ease-in;
    }

    &::before {
      top: -15px;
      border-color: transparent transparent $accentColor transparent;
      animation-name: tableArrowUp;
    }

    &::after {
      bottom: -19px;
      border-color: $accentColor transparent transparent transparent;
      animation-name: tableArrowDown;

      @media (min-width: 1200px) {
        bottom: -21px;
      }

      @media (min-width: 1400px) {
        bottom: -24px;
      }
    }

    transition: all .2s ease-in;
    transform: scale3d(1,1,1);

    &:hover {
      transform: scale3d(1.05,1.05,1);
      transition: all .2s ease-out;

      &::before, &::after {
        width: 1px;
        height: 1px;
        border-width: 10px;
        left: calc(50% - 9px);
        transition: all .2s ease-out;
        animation-iteration-count: 1;
      }

      &::before {
        top: 0;
        border-color: transparent transparent $additionalColor transparent;
      }

      &::after {
        bottom: 0;
        border-color: $additionalColor transparent transparent transparent;
      }
    }
  }

  &__itemWrap {
    overflow: hidden;
    border-radius: $border-radius;
    max-height: 250px;
    box-shadow: 5px 4px 0 0 rgba(36, 163, 243, 0.2);

    @media (min-width: 460px) {
      max-height: 300px;
    }

    @media (min-width: 576px) {
      max-height: 300px;
    }

    @media (min-width: 768px) {
      max-height: 320px;
    }

    @media (min-width: 992px) {
      max-height: 350px;
      box-shadow: 6px 6px 0 0 rgba(36, 163, 243, 0.2);
    }

    @media (min-width: 1400px) {
      box-shadow: 6px 6px 0 0 rgba(36, 163, 243, 0.2);
    }
  }

  &__list {

  }

  &__item {
    border-radius: $border-radius;
    display: block;
    overflow-y: scroll;
    overflow-x: hidden;
    max-height: 250px;
    scrollbar-width: none;

    @media (min-width: 460px) {
      max-height: 300px;
    }

    @media (min-width: 576px) {
      max-height: 300px;
    }

    @media (min-width: 768px) {
      max-height: 320px;
    }

    @media (min-width: 992px) {
      max-height: 350px;
    }


    &::-webkit-scrollbar {
      display: none;
    }
  }

  &__to-more {
    width: 100%;
    height: 60px;
    margin-top: 30px;
  }
}

